import { useEffect, useRef } from 'react';

const createRootElement = (id: string): HTMLElement => {
  const rootEl = document.createElement('div');
  rootEl.setAttribute('id', id);
  document.body.appendChild(rootEl);
  return rootEl;
};

export const usePortal = (id: string): HTMLElement => {
  const rootRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const existingEl = document.querySelector(`#${id}`);
    const parentElem = existingEl || createRootElement(id);
    parentElem.appendChild(rootRef.current);

    return () => rootRef.current.remove();
  }, [id]);

  // https://reactjs.org/docs/hooks-faq.html#how-to-create-expensive-objects-lazily
  const getRootElem = (): HTMLElement => {
    if (!rootRef.current) {
      rootRef.current = document.createElement('div');
    }
    return rootRef.current;
  };

  return getRootElem();
};
